<script type="text/javascript">
import Location from './Product/Location.vue';
	export default {
		data(){
			return {
				operate:true,
				technology:false,
				comprehensive:false
			}
		},
		components:{
			'v-location':Location
		},
		methods:{
			click(ev){
				this.oEvt=ev||event;
				this.id=this.oEvt.target.id||this.oEvt.srcElement.id;
				this.operate=false;
				this.technology=false;
				this.comprehensive=false;
				switch(this.id){
					case 'operate':
						this.operate=true;
					break;
					case 'technology':
						this.technology=true;
					break;
					case 'comprehensive':
						this.comprehensive=true;
					break;
				}
			}
		}
	}
</script>
