<template>
	<div class="plan">
        <div v-for="msg in msgs" class="planbox">
          	<div class="planbox_tit">
            	{{msg.planbox_tit}}
          	</div>
          	<dl>
	            <dt>{{msg.dt}}</dt>
	            <dd v-for="dd in msg.dd">
	              	<img :src="dd.src" :alt="dd.title">
	              	<div>
		                <p class="title">
		                  {{dd.title}}
		                </p>
		                <p>
		                  {{dd.p}}
		                </p>
	              	</div>
	            </dd>
          	</dl>
        </div>
    </div>
</template>
<script type="text/javascript">
	export default {
		data(){
			return{
				msgs:[
				{
					planbox_tit:'',
					dt:'',
					dd:[
						{
							src:require('../../../static/images/16.png'),
							title:'',
							p:''
						},
						{
							src:require('../../../static/images/17.png'),
							title:'SEO',
							p:''
						},
						{
							src:require('../../../static/images/18.png'),
							title:'',
							p:''
						},
						{
							src:require('../../../static/images/19.png'),
							title:'',
							p:'PC8684'
						}
					]
				},
				{
					planbox_tit:'',
					dt:'',
					dd:[
						{
							src:require('../../../static/images/20.png'),
							title:'',
							p:'404URLSitemapMeta'
						},
						{
							src:require('../../../static/images/21.png'),
							title:'',
							p:'SEO'
						},
						{
							src:require('../../../static/images/17.png'),
							title:'SEO',
							p:''
						},
						{
							src:require('../../../static/images/19.png'),
							title:'',
							p:'PC8684'
						}
					]
				}
			]
			}

		}
	}
</script>
<style type="text/css">
	.plan {
	    width: 100%;
	    padding-top: 50px;
	}
	.planbox {
	    width: 1280px;
	    margin: 0 auto;
	    padding-bottom: 50px;
	}
	.planbox_tit {
	    width: 100%;
	    height: 24px;
	    line-height: 24px;
	    font-size: 24px;
	    padding-bottom: 40px;
	    color: #4d4d4d;
	}
	.planbox dl {
	    width: 1180px;
	    overflow: hidden;
	    margin: 0 50px;
	    background: #eeeff1;
	    padding: 30px 0;
	    border-radius: 8px;
	}
	.planbox dl dt {
	    width: 1120px;
	    height: ;
	    float: left;
	    font-size: 25px;
	    padding: 0 30px 10px 30px;
	    color: #4e4e4e;
	}
	.planbox dl dd {
	    width: 590px;
	    height: 132px;
	    float: left;
	    padding: 50px 0;
	}
	.planbox dl dd img {
	    width: 128px;
	    height: 128px;
	    padding: 0 35px;
	    vertical-align: middle;
	}
	.planbox dl dd div {
	    width: 380px;
	    display: inline-block;
	    vertical-align: top;
	}
	.planbox dl dd div p.title {
	    height: 20px;
	    line-height: 20px;
	    font-size: 20px;
	    color: #49a2dc;
	    padding: 0 0 8px;
	}
	.planbox dl dd div p {
	    width: 200px;
	    height: 14px;
	    line-height: 20px;
	    font-size: 14px;
	    color: #4d4d4f;
	    padding-top: 10px;
	}
</style>


