<script type="text/javascript">
	export default{
		data(){
			return {
				about:true,
				culture:false,
				product:false,
				welfare:false
			}
		},
		methods:{
			click(ev){
				this.oEvt=ev||event;
				this.id=this.oEvt.target.id||this.oEvt.srcElement.id;
				this.about=false;
				this.culture=false;
				this.product=false;
				this.welfare=false;
				switch(this.id){
					case 'about':
						this.about=true;
					break;
					case 'culture':
						this.culture=true;
					break;
					case 'product':
						this.product=true;
					break;
					case 'welfare':
						this.welfare=true;
					break;
				}
			}
		}
	}
</script>
